<template>
  <keep-alive>
    <div
      class="skill-set d-flex align-items-center justify-content-center h-100 w-100"
      style="padding-top: 85px"
      :style="{
        height: currentElementHeight,
        minHeight: selected === position ? '90vh' : 'auto'
      }"
    >
      <b-container :id="`container__${position}`">
        <transition mode="out-in" name="slide-fade">
          <div
            :key="title"
            v-html="title"
            :style="{
              marginLeft: selected !== position ? '-10px' : 0
            }"
          ></div>
        </transition>
        <small class="text-secondary" v-if="selected === -1 && position === 0"
          >Click to open</small
        >

        <div
          class="skill-content"
          ref="scrollable"
          :style="{
            height: contentHeight,
            opacity: opacity,
            transitionDelay: transitionDelay
          }"
        >
          <transition name="fade" mode="out-in">
            <div v-if="selected === position">
              <div v-html="skill.description"></div>
              <div v-if="skill.abilities && skill.abilities.length">
                <h4 class="mt-5">Skills and levels</h4>
                <div class="w-100 text-left pl-2">
                  <b-button
                    type="button"
                    @click="order"
                    variant="info"
                    class="mt-4"
                  >
                    Order by
                    {{ orderBy === 0 ? "Proefficiency" : "Prefference" }}
                  </b-button>
                </div>
                  <b-row
                    class="mt-1 pb-4"
                    ref="scrollableContent"
                    :key="orderBy"
                  >
                    <ability-description
                      @modal="$emit('modal', $event)"
                      v-for="(ability, index) in abilities"
                      :key="index"
                      :ability="ability"
                    />
                  </b-row>
              </div>
            </div>
          </transition>
        </div>
      </b-container>
    </div>
  </keep-alive>
</template>

<script lang="ts">
import Vue from "vue";
import AbilityDescription from "./AbilityDescription.vue";
export default Vue.extend({
  components: { AbilityDescription },
  name: "SkillSet",
  data() {
    return {
      transitionDelay: "0ms",
      offset: 85,
      orderBy: 0,
      original: [],
      abilities: []
    };
  },
  props: ["skill", "selected", "position"],
  methods: {
    order: function() {
      if (this.orderBy === 0) {
        this.original = JSON.parse(JSON.stringify(this.abilities));
        const abilities = this.abilities.sort((a, b) =>
          a.level > b.level ? -1 : 1
        );
        this.orderBy = 1;
        this.abilities = abilities;
      } else {
        this.orderBy = 0;
        this.abilities = JSON.parse(JSON.stringify(this.original));
      }
    },
    scroll(direction = "down") {
      const variant = direction === "down" ? 1 : -1;
      for (let i = 0; i < 100; i++) {
        setTimeout(() => {
          this.$refs["scrollable"].scrollTop += variant;
        }, 50 + i * 2);
      }
    },
    currentElementHeight() {
      if (this.$root.isMobile && this.$refs["scrollable"]) {
        const children = this.$refs["scrollable"].childNodes;
        if (Array.isArray(children)) {
          let height = 0;
          children.forEach(item => {
            height += item.clientHeight + 10;
          });
          return height + "px";
        }
      }
      return "100%";
    }
  },
  watch: {
    selected(n, o) {
      if (o === this.position) {
        this.transitionDelay = "0ms";
      } else {
        if (n === this.position && this.$root.isMobile) {
          setTimeout(() => {
            this.$scrollTo(`#container__${this.position}`);
            console.log(`#container__${this.position}`);
          }, 1000);
        }
        this.transitionDelay = "650ms";
      }
    }
  },
  computed: {
    minimumOffsetToMove() {
      return window.innerHeight - (window.innerHeight - 85) / 3;
    },
    currentlySelected() {
      return this.selected === this.position;
    },
    contentHeight() {
      if (!this.$root.isMobile) {
        return this.currentlySelected ? "86.5vh" : "0";
      } else {
        return this.currentlySelected ? this.currentElementHeight() : "0";
      }
    },
    opacity() {
      return this.currentlySelected ? 1 : 0;
    },
    title() {
      if (this.skill.title) {
        return this.selected !== -1 && this.selected !== this.position
          ? `<h5>${this.skill.little}</h5>`
          : `<h2>${this.skill.title}</h2>`;
      }
      return "";
    }
  },
  mounted() {
    this.abilities = this.skill.abilities;
    if (this.$refs["scrollable"]) {
      this.$refs["scrollable"].addEventListener("wheel", $e => {
        if ($e.deltaY > 0) this.scroll("down");
        else this.scroll("up");
      });
    }
  }
});
</script>
<style scoped>
.skill-content {
  transition: ease-in-out 450ms;
  overflow: hidden;
}
.scrolldown-icon,
.scrollup-icon {
  opacity: 0.7;
}
.scrolldown-icon:hover,
.scrollup-icon:hover {
  opacity: 1;
}
.scrolldown-icon {
  background-color: rgba(0, 0, 0, 0.8);
  padding: 0 10px;
  position: absolute;
  bottom: 0px;
  left: calc(50% - 3em);
  cursor: pointer;
}
.scrollup-icon {
  background-color: rgba(0, 0, 0, 0.8);
  padding: 0 10px;
  position: absolute;
  bottom: 0px;
  left: calc(50%);
  cursor: pointer;
}
.flip-list-move {
  transition: transform 0.8s ease;
}
</style>